import React, { forwardRef } from 'react';
import { bool, oneOf, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-card-art';
const CSS_STATE = 'vds-state';

const CardArt = forwardRef(({
  className,
  doubleShimmer,
  doubleWiggle,
  goldRipple,
  muteColor,
  ripple,
  shimmer,
  size,
  unmuteColor,
  wiggle,
  zoomBounce,
  ...remainingProps
}, ref) => (
  <div
    className={classnames(CSS_PREFIX, {
      [`${CSS_STATE}--${size}`]: Boolean(size),
      [`${CSS_STATE}--double-shimmer`]: Boolean(doubleShimmer),
      [`${CSS_STATE}--double-wiggle`]: Boolean(doubleWiggle),
      [`${CSS_STATE}--gold-ripple`]: Boolean(goldRipple),
      [`${CSS_STATE}--mute-color`]: Boolean(muteColor),
      [`${CSS_STATE}--ripple`]: Boolean(ripple),
      [`${CSS_STATE}--shimmer`]: Boolean(shimmer),
      [`${CSS_STATE}--unmute-color`]: Boolean(unmuteColor),
      [`${CSS_STATE}--wiggle`]: Boolean(wiggle),
      [`${CSS_STATE}--zoom-bounce`]: Boolean(zoomBounce)
    }, className)}
    ref={ref}
    {...remainingProps}
  />
));

CardArt.propTypes = {
  /**
   * @ignore
   */
  className: string,

  /**
   * Double Shimmer
   */
  doubleShimmer: bool,

  /**
   * Double Wiggle
   */
  doubleWiggle: bool,

  /**
   * Gold Ripple
   */
  goldRipple: bool,

  /**
   * Mute Color
   */
  muteColor: bool,

  /**
   * Ripple
   */
  ripple: bool,

  /**
   * Shimmer
   */
  shimmer: bool,

  /**
  * Size
  */
  size: oneOf([
    '334x210',
    '286x180',
    '146x92',
    '108x68',
    '48x30',
    '40x25'
  ]),

  /**
   * Unmute Color
   */
  unmuteColor: bool,

  /**
   * Wiggle
   */
  wiggle: bool,

  /**
   * ZoomBounce
   */
  zoomBounce: bool
};

CardArt.displayName = 'CardArt';

export default CardArt;

