import React, { useState } from 'react';
import Col from '../../col';
import Grid from '../../grid';
import Row from '../../row';
import Typography from '../../typography';
import Radio from '../../radio';
import CardArt from '../../card-art';

const CardArtExample = () => {
  const [state, setState] = useState({
    motion: 'doubleShimmer',
    size: '334x210'
  });

  const handleChange = e => setState({ ...state, size: e.target.value });
  const handleMotionChange = e => setState({ ...state, motion: e.target.value });

  return (
    <Grid>
      <Row>
        <Col>
          <Typography tag="h2">Card Art Sizes</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Radio value="334x210" name="card-art-size" label="Card Size 334x210" onChange={handleChange} />
          <Radio value="286x180" name="card-art-size" label="Card Size 286x180" onChange={handleChange} />
          <Radio value="146x92" name="card-art-size" label="Card Size 146x92" onChange={handleChange} />
          <Radio value="108x68" name="card-art-size" label="Card Size 108x68" onChange={handleChange} />
          <Radio value="48x30" name="card-art-size" label="Card Size 48x30" onChange={handleChange} />
          <Radio value="40x25" name="card-art-size" label="Card Size 40x25" onChange={handleChange} />
        </Col>
      </Row>
      <Row>
        <Col>
          <CardArt
            size={state.size}
            style={{
              backgroundImage: `url("http://ux/projects/visa-ui-web/3.0.0-alpha.12/_imgs/cards/visa-cardart-default-${state.size}.png")`
            }}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <Typography tag="h2">Card Art Motion</Typography>
        </Col>
      </Row>
      <Row>
        <Col>
          <Radio
            value="doubleShimmer"
            name="card-art-motion"
            label="Card Art Motion double-shimmer"
            onChange={handleMotionChange}
          />
          <Radio
            value="doubleWiggle"
            name="card-art-motion"
            label="Card Art Motion double-wiggle"
            onChange={handleMotionChange}
          />
          <Radio
            value="goldRipple"
            name="card-art-motion"
            label="Card Art Motion gold-ripple"
            onChange={handleMotionChange}
          />
          <Radio
            value="muteColor"
            name="card-art-motion"
            label="Card Art Motion mute-color"
            onChange={handleMotionChange}
          />
          <Radio
            value="ripple"
            name="card-art-motion"
            label="Card Art Motion ripple"
            onChange={handleMotionChange}
          />
          <Radio
            value="shimmer"
            name="card-art-motion"
            label="Card Art Motion shimmer"
            onChange={handleMotionChange}
          />
          <Radio
            value="unmuteColor"
            name="card-art-motion"
            label="Card Art Motion unmute-color"
            onChange={handleMotionChange}
          />
          <Radio
            value="wiggle"
            name="card-art-motion"
            label="Card Art Motion wiggle"
            onChange={handleMotionChange}
          />
          <Radio
            value="zoomBounce"
            name="card-art-motion"
            label="Card Art Motion zoom-bounce"
            onChange={handleMotionChange}
          />
        </Col>
      </Row>
      <Row>
        <Col>
          <CardArt
            doubleShimmer={state.motion === 'doubleShimmer' || undefined}
            doubleWiggle={state.motion === 'doubleWiggle' || undefined}
            goldRipple={state.motion === 'goldRipple' || undefined}
            muteColor={state.motion === 'muteColor' || undefined}
            ripple={state.motion === 'ripple' || undefined}
            shimmer={state.motion === 'shimmer' || undefined}
            unmuteColor={state.motion === 'unmuteColor' || undefined}
            wiggle={state.motion === 'wiggle' || undefined}
            zoomBounce={state.motion === 'zoomBounce' || undefined}
            style={{
              backgroundImage: `url("http://ux/projects/visa-ui-web/3.0.0-alpha.12/_imgs/cards/visa-cardart-default-334x210.png")`
            }}
          />
        </Col>
      </Row>
    </Grid>
  );
};

export default CardArtExample;
